import { useEffect, useState } from 'react';
import { METHOD } from 'constants/index';
import apiClient from 'api';
import PropTypes from 'prop-types';
// import axios from 'axios';

const useFetch = ({ method, url, handler }) => {
  const [isLoading, setIsLoading] = useState(false);
  const [isError, setIsError] = useState(false);
  const [data, setData] = useState(null);

  console.log('url', url);

  const fetchApi = async (params = '') => {
    setIsLoading(true);
    setIsError(false);
    try {
      console.log('들어오긴 함?');
      // const { data } = await apiClient[method](`${url}/${params}`);
      const res = await apiClient[method](`${url}/${params}`);

      console.log('----------res', res);
      const { data } = res;
      console.log('data', data);
      setData(data);
    } catch (error) {
      setIsError(true);
    } finally {
      setIsLoading(false);
    }
  };

  useEffect(() => {
    if (data && handler) {
      handler(data);
    }
  }, [data]);

  return { isLoading, isError, data, fetchApi };
};

useFetch.propTypes = {
  method: PropTypes.oneOf(Object.values(METHOD)),
  url: PropTypes.string,
};

export default useFetch;
