import axios from 'axios';
import { setCookie, getCookie } from 'utils/cookie';

const COOKIE_KEY = process.env.REACT_APP_AUTH_COOKIE_KEY;

const getAuthorizationToken = () => {
  const accessToken = getCookie(COOKIE_KEY);
  return `Bearer ${accessToken}`;
};
// Wooteco1!
// const BASE_URL = '13.125.96.150:8080';
// const BASE_URL = 'http://54.180.123.9:8080';

const BASE_URL = process.env.REACT_APP_API_ARI_URL;

/**
 * 회원가입 0
 * 로그인 0
 * 회원 탈퇴 0
 * 이름 수정 0
 * 비밀번호 수정 0
 * 회원 정보 조회 0
 * 중복 이메일 확인 0
 * 장바구니 목록 조회
 */

// 로그인
export const loginApi = async (payload) => {
  try {
    console.log('loginApi payload 로그인', payload);
    const { email, password } = payload;
    const res = await axios({
      method: 'POST',
      url: `${BASE_URL}/api/auth`,
      data: { email, password },
    });

    const {
      data: { accessToken },
    } = res;
    console.log('accessToken', accessToken);
    setCookie(COOKIE_KEY, accessToken);
  } catch (err) {
    console.log('err', err);
  }
};

// 회원 정보 조회
export const getUserApi = async () => {
  try {
    console.log('');
    const res = await axios({
      method: 'GET',
      url: `${BASE_URL}/api/members/me`,
      headers: {
        Authorization: getAuthorizationToken(),
      },
    });

    const { id, name, email } = res.data;

    return { id, name, email };
  } catch (err) {
    console.log('err', err);
  }
};

// 회원가입
export const signUpApi = async (payload) => {
  try {
    const { email, password, name } = payload;
    await axios({
      method: 'POST',
      url: `${BASE_URL}/api/members`,
      data: { email, password, name },
      headers: {
        'Content-Type': 'text/xml',
      },
    });
  } catch (err) {
    console.log('err', err);
  }
};

