// 이름 수정
export const updateNameApi = async (payload) => {
  try {
    console.log(payload);

    const { name } = payload;

    await axios({
      method: 'PUT',
      url: `${BASE_URL}/api/members/me/name`,
      headers: {
        Authorization: getAuthorizationToken(),
      },
      data: { name },
    });
  } catch (err) {
    console.log('err', err);
  }
};

// 비밀번호 수정
export const updatePasswordApi = async (payload) => {
  try {
    const { oldPassword, newPassword } = payload;
    await axios({
      method: 'PUT',
      url: `${BASE_URL}/api/members/me/password`,
      headers: {
        Authorization: getAuthorizationToken(),
      },
      data: { oldPassword, newPassword },
    });
  } catch (err) {
    console.log('err', err);
  }
};

// 회원 탈퇴
export const unRegisterApi = async (payload) => {
  try {
    console.log('unRegisterApi payload', payload);
    await axios({
      method: 'DELETE',
      url: `${BASE_URL}/api/members/me`,
      headers: {
        Authorization: getAuthorizationToken(),
      },
    });
  } catch (err) {
    console.log('err', err);
  }
};
